.sidebar .container,
.main .container {
  margin-top: 20px;
}

ol {
  margin-left: 20px;
}

ol li {
  margin: 0 0 15px 0;
  padding-left: 10px;
}

.main h5 {
  margin-top: 15px;;
}

.sticky {
  position: sticky;
  top: 15px;
}

.sidebar .notification {
  padding: 20px;
}
.sidebar .notification .icon {
  margin: 10px auto 30px;
}


.footer {
  margin-top: 20px;
}

.hero-icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.hero-icons .link-icon i {
  color: white;
}

.hero-icons .link-icon + .link-icon {
  margin-left: 40px;
} 